// PaginaCrearCampaa.js
import React from 'react';
import Campaacrear from '../campaa/campaacrear';
import './campaacrear.css';  // Importa el archivo de estilo CSS

function PaginaCrearCampaa() {
  return (
    <div className='bg-paginas bg-crearCampaa'>
      <div className='container'>
        <h1 className='titulo' style={{ fontSize: '2rem' }}>Crea tu campaa de donacin  </h1>
        <div className='contenido'>
          <div className='row align-items'>
            <div className='col'>
              <div className='logo-overlay'>
                <img
                  src='./ilustraciones/campaa.jpg'
                  alt='Descripcin de la imagen'
                  style={{ maxWidth: '100%', height: 'auto' }}
                />

                <p>
                  <strong>nete a nosotros para crear un impacto significativo! </strong>
                </p>

                <p>
                  
                  Completar el formulario es el primer paso hacia la realizacin de proyectos valiosos.
                </p>

                <p>
                  Comparte tu campaa con amigos, familiares y la comunidad, inspirndolos a contribuir a 
                  causas importantes. 
                </p>

                <p>
                  Trabaja hacia tu objetivo con el apoyo de aquellos que comparten tu causa. 
                  Cada contribucin cuenta y hace la diferencia en la vida de quienes beneficiarn de tu generosidad. 
                </p>
              </div>
            </div>
            <div className='col-md-6'>
              <div className='acomodar-crearCampaa'>
                <Campaacrear />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PaginaCrearCampaa;
