import React from 'react';
import IniciarSesion from '../Iniciar-sesin/Iniciar-sesin';
import './PaginaLogin.css'; // Asegrate de importar el archivo CSS

function PaginaLogin() {
  return (
    <div className='bg-paginas bg-registro'>
      <div className="container container-blanco">
        <div className="row">
          <div className="col">
            <img src="./logos/sinergia-imagotipo-oscuro copia 2.svg" alt="Logo" />
            <div className="logo-overlay">
              <h1>Bienvenido de vuelta a Sinergia!</h1> <br></br>
              <p>En Sinergia, cada inicio de sesin es un paso ms hacia la creacin de un impacto positivo.</p>
              <p><strong>Tu cuenta, tu impacto.</strong></p>
              <p>Inicia sesin hoy y contina tu viaje hacia una vida llena de propsito y significado.</p>
              <p><strong>La magia sucede cuando te involucras. Vamos, haz clic y forma parte del cambio!</strong></p>
            </div>
          </div>
          <div className="col">
            <div className="acomodar-registro-login">
              <IniciarSesion />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PaginaLogin;
