
import React from 'react';
import './App.css';
import { BrowserRouter, Route, Routes, useLocation, Navigate } from 'react-router-dom';
import PaginaInicio from './components/pagina-inicio/pagina-inicio';
import PaginaNosotros from './components/pagina-nosotros/pagina-nosotros';
import PaginaBuscador from './components/pagina-buscador/PaginaBuscador'
import PaginaPerfil from './components/pagina-perfil/pagina-perfil'; 
import Navbar from './components/Navbar/Navbar';
import Footer from "./components/Footer/Footer";
import PaginaCampaa from './components/pagina-campaa/pagina-campaa';
import PaginaRegistro from './components/pagina-registro/pagina-registro';
import PaginaLogin from './components/pagina-login/pagina-login';
import Pagina_Tus_Campaas from './components/pagina-tus-campaas/pagina-tus-campaas';
import PaginaCrearCampaa from './components/pagina-crear-campaa/pagina-crear-campaa';
import PaginaRecuperarContrasea from './components/Pagina-recuperar-contrasea/pagina-recuperar-contrasea';
import getRol from './components/funcion-getRol/funcion-getRol';
import getAutenticacion from './components/funcion-autenticacion/autenticacion';

function Main() {

  const location = useLocation();
  const hideForRegistro = location.pathname !== "/registro";
  const hideForLogin = location.pathname !== "/login";
  const hideForContrasena = location.pathname !== "/recuperar-contrasena";
  const rolUsuario = getRol()
  const AutenticacionUser = getAutenticacion()

  if (rolUsuario !== 'institucin' && location.pathname === '/tus-campaas') {
    return <Navigate to='/' />;
  }

  if (AutenticacionUser !== true && (location.pathname === '/' || location.pathname === '/nosotros' || location.pathname === '/buscar' || location.pathname === '/donaciones' || location.pathname === '/perfil' || location.pathname === '/ficha-campaa' || location.pathname === '/tus-campaas' || location.pathname === '/crear-campaa')) {
    return <Navigate to='/registro' />;
  }

  return (
      <div>
          {hideForRegistro && hideForLogin && hideForContrasena && <Navbar/> }
          <Routes>
            {/* Pginas Pblicas */}
            <Route path='/registro' element={<PaginaRegistro />} />
            <Route path='/login' element={<PaginaLogin />} />
            <Route path='/recuperar-contrasena' element={<PaginaRecuperarContrasea />} />

            {/* Pginas Privadas */}
            <Route path='/' element={<PaginaInicio />} />
            <Route path='/nosotros' element={<PaginaNosotros />} /> 
            <Route path='/buscar' element={<PaginaBuscador />} />
            <Route path='/perfil' element={<PaginaPerfil />} />
            <Route path='/ficha-campaa/:campaignId' element={<PaginaCampaa />} />
            <Route path='/tus-campaas' element={<Pagina_Tus_Campaas />} />
            <Route path='/crear-campaa' element={<PaginaCrearCampaa />} />
          </Routes>
          {hideForRegistro && hideForLogin && hideForContrasena && <Footer/> }
      </div>
  );
}

function App() {
  return (
      <BrowserRouter>
        <Main />
      </BrowserRouter>
  );
}

export default App;
