import React, { useState } from "react";
import "react-quill/dist/quill.snow.css";
import ReactQuill from "react-quill";
import "./campaacrear.css";
function CreateCampaign() {
  // Estados
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [meta, setMeta] = useState("");
  const [account, setAccount] = useState("");
  const [category, setCategory] = useState("");
  const [banco, setBanco] = useState("");
  const [accountType, setAccountType] = useState("");
  const [accountNum, setAccountNum] = useState("");
  const [email, setEmail] = useState("");
  const [filename, setFileName] = useState(null);
  const [mostrarOpcionesCuenta, setMostrarOpcionesCuenta] = useState(false);
  const regiones = [
    "Regin de Arica y Parinacota",
    "Regin de Tarapac",
    "Regin de Antofagasta",
    "Regin de Atacama",
    "Regin de Coquimbo",
    "Regin de Valparaso",
    "Regin Metropolitana",
    "Regin del Libertador General Bernardo O'Higgins",
    "Regin del Maule",
    "Regin de uble",
    "Regin del Biobo",
    "Regin de La Araucana",
    "Regin de Los Ros",
    "Regin de Los Lagos",
    "Regin de Aysn del General Carlos Ibez del Campo",
    "Regin de Magallanes y de la Antrtica Chilena",
  ];
  //Funcin para que no se vean las <p> en la descripcin
  // Manejadores de cambios
  const handleNombreChange = (event) => {
    setTitle(event.target.value);
  };
  const handleDescripcionChange = (e) => {
    setDescription(e.target.value);
  };
  const [region, setRegion] = useState("");
  const handleRegionChange = (event) => {
  setRegion(event.target.value);
  };
  const handleRangoMetaChange = (event) => {
    setMeta(event.target.value);
  };
  const handleCuentaChange = (event) => {
    setAccount(event.target.value);
  };
  const handleCategoriaChange = (event) => {
    setCategory(event.target.value);
  };
  const handleFotoChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setFileName(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };
  const handleBancoChange = (event) => {
    setBanco(event.target.value);
  };
  const handleTipoCuentaChange = (event) => {
    setAccountType(event.target.value);
  };
  const handleNumeroCuentaChange = (event) => {
    setAccountNum(event.target.value);
  };
  const handleCorreoElectronicoChange = (event) => {
    setEmail(event.target.value);
  };
  // Funcin para limpiar campos
  const limpiarCampos = () => {
    setTitle("");
    setDescription("");
    setMeta("");
    setAccount("");
    setCategory("");
    setFileName(null);
    setBanco("");
    setAccountType("");
    setAccountNum("");
    setEmail("");
  };
  // Funcin para manejar el clic en Mostrar/Ocultar opciones de cuenta
  const toggleOpcionesCuenta = () => {
    setMostrarOpcionesCuenta(!mostrarOpcionesCuenta);
  };
  // Funcin para manejar el envo del formulario
  const handleFormSubmit = (event) => {
    event.preventDefault();
    if(!title || !description || !meta || !category || !banco || !accountType || !accountNum || !email){
      console.log("todos los campos son requeridos");
      return;
    }
    alert("La campaa se ha creado con xito");
    // Aqu puedes implementar la lgica para enviar los datos del formulario
    const formulario = {
      title,
      description,
      meta,
      category,
      account : {
        banco,
        accountType,
        accountNum,
        email,
        filename,
      }
    };
    console.log("Formulario a enviar:", formulario);
    const token = window.localStorage.getItem("token");
    fetch("http://localhost:8080/api/campanas", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        credentials : 'include',
        acces_token: token
      },
      body: JSON.stringify(formulario)
    })
    .then(res => res.json())
    .then(res => console.log(res))
};
return (
  <form id="formulario">
  <div className="contenedor" style={{ fontFamily: "Montserrat" }}>
    <h1 className="titulo">Nueva campaa / Editar Campaa </h1>
    <div className="formulario">
      {/* Nombre de campaa */}
      <div>
        <p className="subtitulo">Nombre de campaa</p>
        <input
          className="entrada"
          type="text"
          value={title}
          onChange={handleNombreChange}
          placeholder="Ingrese su nombre de campaa"
        />
      </div>
      {/* Descripcin */}
      <div>
        <p className="subtitulo">Descripcin de campaa</p>
        {/* <ReactQuill
          className="quill-editor"
          value= {description}
          onChange={handleDescripcionChange}
          placeholder="Describa la campaa"
        /> */}
        <input
          className="entrada"
          type="text"
          value= {description}
          onChange={handleDescripcionChange}
          placeholder="Describa la campaa"
        />
      </div>
      {/* Regiones */}
<div>
<p className="subtitulo">Regin</p>
<select
  className="entrada"
  value={region}
  onChange={handleRegionChange}
>
  <option value="" disabled defaultValue>
    Seleccione una regin
  </option>
  {regiones.map((region, index) => (
    <option key={index} value={region}>
      {region}
    </option>
  ))}
</select>
</div>
     {/* META */}
      <div>
        <p className="subtitulo">Rango Meta</p>
        <input
          className="entrada"
          type="number"
          value={meta}
          onChange={handleRangoMetaChange}
          placeholder="Ingrese el monto en pesos chilenos"
        />
      </div>
      {/* CUENTA */}
      <div>
        <p className="subtitulo" onClick={toggleOpcionesCuenta}>
          Cuenta {mostrarOpcionesCuenta ? "" : ""}
        </p>
        {mostrarOpcionesCuenta && (
          <div className="opciones-cuenta">
            {/* Banco */}
            <div>
              <p className="subtitulo">Banco</p>
              <select
                className="entrada"
                value={banco}
                onChange={handleBancoChange}
              >
                <option value="" disabled defaultValue>
                  Seleccione un banco
                </option>
                <option value="BANCO DE CHILE/EDWARDS CITI">BANCO DE CHILE/EDWARDS CITI</option>
                <option value="BANCO ESTADO">BANCO ESTADO</option>
                <option value="SCOTIABANK">SCOTIABANK</option>
              </select>
            </div>
            {/* Tipo de Cuenta */}
            <div>
              <p className="subtitulo">Tipo de Cuenta</p>
              <select
                className="entrada"
                value={accountType}
                onChange={handleTipoCuentaChange}
              >
                <option value="" disabled defaultValue>
                  Seleccione un tipo de cuenta
                </option>
                <option value="Cuenta Vista">Cuenta Vista</option>
                <option value="Cuenta Corriente">Cuenta Corriente</option>
                <option value="Cuenta Rut">Cuenta Rut</option>
              </select>
            </div>
            {/* Nmero de Cuenta */}
            <div>
              <p className="subtitulo">Nmero de Cuenta</p>
              <input
                className="entrada"
                type="text"
                value={accountNum}
                onChange={handleNumeroCuentaChange}
                placeholder="Ingrese su nmero de cuenta"
              />
            </div>
            {/* Correo Electrnico */}
            <div>
              <p className="subtitulo">Correo Electrnico</p>
              <input
                className="entrada"
                type="email"
                value={email}
                onChange={handleCorreoElectronicoChange}
                placeholder="Ingrese su correo electrnico"
              />
            </div>
          </div>
        )}
      </div>
      {/* CATEGORA */}
      <div>
        <p className="subtitulo">Categora</p>
        <select
          className="entrada"
          value={category}
          onChange={handleCategoriaChange}
        >
          <option value="" disabled defaultValue>
            Seleccione una categora
          </option>
          <option value="Vivienda">Vivienda</option>
          <option value="Salud">Salud</option>
          <option value="Educacin">Educacin</option>
          <option value="Infancia">Infancia</option>
          <option value="Rural">Rural</option>
        </select>
      </div>
      {/* Fotos */}
      {/* <div>
        <p className="subtitulo">Foto</p>
        {filename && (
          <div className="imagen-contenedor">
            <img
              src={"#"}
              name = "image"
              alt="Imagen de la campaa"
              className="imagen-preview"
            />
            <button className="boton-eliminar" onClick={() => setFileName(null)}>
              Eliminar Foto
            </button>
          </div>
        )}
          <input
            className="entrada"
            type="file"
            accept="image/*"
            src={filename}
            onChange={handleFotoChange}
          />
      </div> */}
      {/* Botones */}
      <div className="botones-container">
        <div className="boton-div">
          <button className="boton" onClick={limpiarCampos}>
            Reiniciar
          </button>
        </div>
        <div className="boton-div">
          <button className="boton" onClick={handleFormSubmit}>
            Enviar
          </button>
        </div>
      </div>
    </div>
  </div>
  </form>
);
}
export default CreateCampaign;