import React, { useState, useEffect } from 'react';
import SubsCampaaDescripcion from '../subs-campaa-descripcion/subs-campaa-descripcion';
import SubsCampaaStatus from '../subs-campaa-status/subs-campaa-status';
import SubsCampaaDonar from '../subs-campaa-donar/subs-campaa-donar';

function PaginaCampaa () {

    const [campaign, setCampaign] = useState(null);

  useEffect(() => {

    const pathname = window.location.pathname;
    const parts = pathname.split('/');
    const campaignId = parts[parts.length - 1];
    const token = localStorage.getItem("token");

    // Realizar la peticin para obtener detalles de la campaa especfica
    fetch(`http://localhost:8080/api/campanas/${campaignId}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        credentials : 'include',
        acces_token: token,
        // Agrega tu lgica de manejo de tokens aqu si es necesario
      },
    })
      .then((res) => res.json())
      .then((data) => {
        // Actualizar el estado con los detalles de la campaa
        setCampaign(data);
      })
      .catch((error) => {
        console.error('Error al obtener detalles de la campaa:', error.message);
      });
  }, []); // Asegrate de manejar las dependencias segn tu lgica

  if (!campaign) {
    // Puedes mostrar un indicador de carga mientras se obtienen los datos
    return <p>Cargando...</p>;
  }
  
  /********* */
    
    return(
        <div className='bg-paginas'>
            <SubsCampaaDescripcion title={campaign.title} description={campaign.description} img={campaign.img}/>
            {/* <SubsCampaaStatus /> */}
            <SubsCampaaDonar />
        </div>
    );
};

export default PaginaCampaa;